@import 'ScssHelpers/helpers-all.scss';

.get_social {
	&.get_social--block_mod {
		padding: 6.6rem 0 2rem;
	}

	@include bp($point_2, min) {
		padding: 10rem var(--gl-indent) 18rem;
	}

	@include bp($point_3) {
		padding: 4.8rem var(--gl-indent) 5.2rem;
	}
}

.get_social__body {
	position: relative;
}

.get_social__link {
	position: relative;
	display: block;

	&:not(.main_content__slider &) {
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;

			display: block;
			width: 100%;
			height: 100%;

			border-radius: $gl_radius;

			box-shadow: -3.1rem 3.9rem 9rem rgba(20, 20, 20, 0.2);
			transition: opacity 0.3s ease;
			opacity: 0;
		}

		&:hover {
			&:after {
				opacity: 1;
			}
		}
	}

}

.get_social__header {
	text-align: center;

	&:not(:last-child) {
		.get_social--block_mod & {
			margin-bottom: 3rem;
		}

		@include bp($point_2, min) {
			margin-bottom: 6rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}
}

.get_social__title {
	font-family: $font_2;
	@include h2;

	.get_social--block_mod & {
		text-align: left;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.2rem;
		}
	}
}

.get_social__subtitle {
	color: $black_2;

	@include bp($point_2, min) {
		font-size: 1.9rem;
		font-weight: 400;
		line-height: 1.3;
		letter-spacing: 0.02em;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		letter-spacing: 0.02em;
		line-height: 1.4;
	}

	a {
		color: $black_2;
		font-weight: 700;

		border-bottom: 2px solid $black_2;

		&:hover,
		&:focus {
			text-decoration: none;
			border: none;
		}
	}
}

.get_social__slider {
	overflow: visible;

	@include bp($point_2, min) {
		padding-bottom: 6.1rem;

		.get_social--block_mod & {
			padding-bottom: 5rem;
		}
	}

	@include bp($point_3) {
		padding-bottom: 3.1rem;
	}
}

.get_social__slider_item {


	@include bp($point_2, min) {
		max-width: 38.5rem;

		.get_social--block_mod & {
			max-width: 34rem;
		}
	}

	@include bp($point_3) {
		max-width: 22.5rem;
	}
}

.get_social__picture {
	position: relative;

	display: block;

	padding-bottom: 100%;

	border-radius: $gl_radius;
	overflow: hidden;
}

.get_social__img {
	position: absolute;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;

	object-fit: cover;

	transition: transform 0.3s ease;


	.main_content__slider & {
		&:hover {
			transform: scale(1.1);
		}
	}
}
