@import 'ScssHelpers/helpers-all.scss';

.join_our_newsletter {
	@include bp($point_2, min) {
		padding-top: 15.4rem;
		padding-bottom: 15.4rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 20rem;
		padding-bottom: 15.4rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 7.4rem;
		padding-bottom: 8.4rem;
	}
}

.join_our_newsletter__content {
	position: relative;
	z-index: 3;
	max-width: 74rem;
	margin: 0 auto;
	text-align: center;
}

.join_our_newsletter__title {
	@include h2;
	font-family: $font_2;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include bp($point_4 - 1) {
		max-width: 25rem;
		margin: 0 auto;
	}
}

.join_our_newsletter__text {
	@include text;

	color: $black_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.4rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_4 - 1) {
		max-width: 31rem;
		margin: 0 auto;
	}
}

.join_our_newsletter__form__wrap {
	position: relative;
}

.join_our_newsletter__form_input {
	width: 100%;
}

.join_our_newsletter__form_btn {
	position: absolute;
	top: 50%;
	right: 0;

	height: 100%;
	padding-right: 0.4rem;

	font-weight: 700;
	font-size: 1.6rem;
	line-height: 137%;
	font-family: $font_1;

	letter-spacing: 0.03em;

	transform: translateY(-50%);
	cursor: pointer;

	border-left: 1px solid $black_2;

	transition: background 0.3s ease, color 0.3s ease;

	&:focus,
	&:hover {
		@include bp($point_2, min) {
			background-color: $black_2;
			color: $white_2;
		}

		@include bp($point_3) {
			background-color: $white_2;
			color: $black_2;
			border: 1px solid $black_2;
		}
	}

	@include bp($point_2, min) {
		width: 21rem;
		color: $black_2;
		border-radius: 0 7.7rem 7.7rem 0;
	}

	@include bp($point_3) {
		background-color: $black_2;
		color: $white_2;
		border-radius: 0 $gl_radius $gl_radius 0;
	}

	@include bp($point_3, $point_7) {
		width: 15.2rem;
	}

	@include bp($point_7 - 1) {
		width: 10rem;
	}
}
