@import 'ScssHelpers/helpers-all.scss';

.news_banner {
	overflow: hidden;

	&:not(.latest_news_banner &) {
		@include bp($point_2, min) {
			border-radius: $gl_radius;
		}
	}

	&:not(.news_banner--bg_black_mod, .news_banner--bg_green_mod) {
		&.news_banner:nth-child(odd) {
			background: $coral;
		}

		&.news_banner:nth-child(even) {
			background: $green;
		}
	}

	&.news_banner--bg_black_mod {
		background-color: $black_2;
	}

	&.news_banner--bg_green_mod {
		background-color: $green;
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1);
	}
}

.news_banner__row {
	display: flex;
	align-items: stretch;

	@include bp($point_3) {
		flex-direction: column;
	}
}

.news_banner__col {
	@include bp($point_2, min) {
		width: 50%;
		flex-shrink: 0;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			.news_banner--revers_mod & {
				order: 2;
			}
		}

		@include bp($point_3) {
			.venues_child & {
				order: 2;
			}
		}
	}

	&:nth-child(2n + 2) {
		.news_banner--revers_mod & {
			@include bp($point_2, min) {
				order: 1;
			}
		}

		@include bp($point_2, min) {
			padding: var(--gl-indent) 13.4rem var(--gl-indent) var(--gl-indent);

			.news_banner--bg_green_mod & {
				padding: 9rem 9.4rem 3rem 10rem;
			}
		}

		@include bp($point_3) {
			padding: 5.6rem 3.4rem 9.8rem 4rem;
		}
	}
}

.news_banner__pic {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;

	&::after {
		content: '';
		@include bp($point_3) {
			display: block;
			padding-bottom: 84.6%;
		}
	}
}

.news_banner__img {
	@include cover_img;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.news_banner__wrap {
	@include bp($point_2, min) {
		max-width: 39.8rem;
		margin: 0 auto;

		.news_banner--bg_green_mod & {
			max-width: 52rem;
		}
	}
}

.news_banner__content {
	.news_banner--bg_black_mod & {
		color: $white_2;
	}

	&:not(:last-child) {
		margin-bottom: 3rem;

		@include bp($point_2, min) {
			.news_banner--bg_black_mod & {
				margin-bottom: 6.6rem;
			}
		}
	}

	h6 {
		font-weight: 700;
		letter-spacing: 0.2em;
		text-transform: uppercase;
		font-size: 1.6rem;
		line-height: 262%;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.4rem;
			}
			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}
	}

	h3 {
		max-width: 27rem;
		@include h3;
		font-family: $font_2;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.4rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.4rem;
			}
		}
	}

	p {
		@include text;

		&:not(:last-child) {
			margin-bottom: 3rem;

			@include bp($point_3) {
				.news_banner--bg_black_mod & {
					margin-bottom: 1.4rem;
				}
			}
		}
	}
}

.news_banner__list {
}

.news_banner__item {
	p {
		font-weight: 400;
		font-style: italic;
		font-size: 2rem;
		line-height: 140%;
		letter-spacing: 0.03em;

		strong {
			position: relative;
			font-weight: 500;
			font-style: normal;
			text-decoration: underline;
		}
	}

	span {
		@include text;
		color: $black_2;
		.news_banner--bg_black_mod & {
			color: $white_2;
		}
	}

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.news_banner__item_heading {
	@include bp($point_2, min) {
		font-weight: 500;
		font-size: 2rem;
		line-height: 130%;
	}

	@include bp($point_3) {
		font-weight: 700;
	}

	&:not(:last-child) {
		margin-right: 0.4rem;
	}
}

.news_banner__item_link {
	@include bp($point_2, min) {
		font-size: 1.4rem;
		font-style: italic;
		line-height: 128%;
	}

	@include bp($point_3) {
		&::after {
			content: '';
			display: none;
		}
	}
}
.news_banner__item_heading,
.news_banner__item_link {
	position: relative;

	color: $black_2;

	transition: color 0.3s ease;

	.news_banner--bg_black_mod & {
		color: $white_2;
	}

	&::after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 1px;

		background-color: $black_2;

		transition: background 0.3s ease;

		.news_banner--bg_black_mod & {
			background-color: $white_2;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: $martini;

		&::after {
			background-color: $martini;
		}
	}

	@include bp($point_2, min) {
		letter-spacing: 0.03em;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 140%;
		letter-spacing: 0.02em;
	}
}
